    pagecontent:'<h1>多国发生疫情灾难 联合国宣布全球进入紧急状态？</h1><p>近日有消息称，联合国发布通知称：全球进入紧急状态。</p><p>真的是祸不单行？这个世界怎么了？</p><p>发出消息的文章称：全球多个国家发生疫情灾难，巴基斯坦和非洲肯尼亚、索马里、埃塞俄比亚等国还面临严重的沙漠蝗灾等，联合国对此宣布全球进入紧急状态。</p><p>不过，中新社国是直通车记者经多方查证，截至目前，联合国层面并未发出“全球进入紧急状态”的通知。</p><p>对于蝗虫疫情，联合国粮农组织官方网站近期发布新闻显示，呼吁采取更多行动以防沙漠蝗虫疫情造成人道主义危机，但没有提到“全球进入紧急状态”。</p><p class="f_center"><img alt="多国发生疫情灾难 联合国宣布全球进入紧急状态？" src="http://cms-bucket.ws.126.net/2020/0215/65ae7097j00q5pqz9000xc000fa009tc.jpg"></p><p class="f_center">图片来源：联合国粮农组织官方网站</p><p>据了解，巴基斯坦和非洲多国确实面临严重蝗虫灾害，巴基斯坦政府和索马里等国宣布进入国家紧急状态，但联合国层面并未作出全球进入紧急状态的声明。</p><p class="f_center"><img alt="多国发生疫情灾难 联合国宣布全球进入紧急状态？" src="http://cms-bucket.ws.126.net/2020/0215/800a6f2dj00q5pqz9000wc000fa009fc.jpg"></p><p class="f_center">图片来源：央视财经新闻</p><p>据卡塔尔半岛电视台2月2日报道，索马里也是宣布蝗灾为国家紧急状态的国家之一。</p><p class="f_center"><img alt="多国发生疫情灾难 联合国宣布全球进入紧急状态？" src="http://cms-bucket.ws.126.net/2020/0215/b1505021j00q5pqz90013c000fa00brc.jpg"></p><p class="f_center">图片来源：卡塔尔半岛电视台</p><p>中新社国是直通车记者联系的几位联合国相关机构人员均表示，未看到联合国层面对此发出的“全球进入紧急状态”的消息来源。</p><p>在联合国中英文官方网站上也并未发现相关信息。多家国内外官方媒体亦没有看到相关报道。</p><p>对此，联合国有关机构人员表示，联合国发布的消息须有官方和权威消息来源为准，不要轻信一些民间消息，引起不必要恐慌。</p><p><!--StartFragment--><!--EndFragment--></p><p>与其夸大其辞造成不必要的恐慌，不如我们一起努力，抵御灾情，希望地球变得更美好。</p><p><!--EndFragment--></p><p><!-- AD200x300_2 -->',
